import React from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { Link, useHistory, useLocation } from "react-router-dom";
import bg from '../assets/images/sectionBg.png';
// import logBg from '../assets/images/logBg.gif';
import useContexts from "../hooks/useContexts.js";
import "./../assets/css/login.css";
const Login = () => {
  const { userLogin, loading, email } = useContexts();
  const { register, handleSubmit } = useForm();
  const history = useHistory();
  const location = useLocation();
  const redirect = location?.state?.from || "/home";
  const onSubmit = (data) => {
    userLogin({ ...data, history, redirect });
  };
  if (email) {
    return <>{history.replace("/")}</>;
  } else {
    return (
      <div
        style={{
          background: `url(${bg})`,
          backgroundRepeat: "repeat",
          backgroundPosition: "center center",
          backgroundColor: "black",
          height: "100vh"
        }}
      >
        <div className="container">
          <Row>
            <Col xs={12} md={6} className="log-left">
              <h1 className="text-white text-center">Login</h1>
              <hr className="text-white" />
              <h2 className="text-white mt-5  text-center"
                style={{ fontFamily: "sans-serif", paddingTop: "2em" }}
              >
                Good To See You Again Here
              </h2>
              <br />
              <p className="text-white" style={{ fontFamily: "sans-serif" }}>By Logging Into Bikers' Paradise <br /> You Accept Our <Link style={{ color: "white" }}>Terms Of Use</Link> And <Link style={{ color: "white" }}>Privacy Policy</Link> </p>
              <div className="container-fluid">
                {/* <img style={{ height: "10em", marginLeft: "10em", marginTop: "2em" }} src={logBg} alt="" /> */}
              </div>
            </Col>
            <Col xs={12} md={6} className="log-right text-center">
              <Button className="mt-3"><i class="fab fa-google"></i> | Continue With Google</Button>
              <hr />
              <Form className="px-4 py-3 mt-5">
                <Form.Group className="mb-3" controlId="formGroupEmail">
                  <Form.Control type="email" placeholder="Enter Your E-mail" />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formGroupPassword">
                  <Form.Control type="password" placeholder="Enter Your Password" />
                </Form.Group>
                <Link style={{ textDecoration: "none", fontFamily: "cursive", color: "Black", fontWeight: "bold"}}>Forgot Password?</Link>
                <br />
                <Button className="mx-5 px-5 my-2">Login</Button>
              </Form>
              <Link to="/Register" style={{ textDecoration: "none", fontFamily: "cursive", color: "Black", fontWeight: "bold"}}>Register Here</Link>
            </Col>
          </Row>
        </div>
      </div>
      // <div className="login-page">
      //   <div
      //     style={{ height: "100vh" }}
      //     className="d-flex mx-3 align-items-center justify-content-center"
      //   >
      //     <div className="formContainer">
      //       <div className="text-center">
      //         <img width="120px" src={avatar} alt="" />
      //       </div>
      //       <h2 className="text-center text-white">Please Login</h2>
      //       <form className="loginForm" onSubmit={handleSubmit(onSubmit)}>
      //         <input
      //           className="form-control"
      //           type="email"
      //           required
      //           {...register("email", { required: true })}
      //           placeholder="Enter your email"
      //         />
      //         <input
      //           required
      //           type="password"
      //           className="form-control"
      //           {...register("password", { required: true })}
      //           placeholder="Enter your password"
      //         />
      //         <button className="btn register-btn" type="submit">
      //           {loading ? (
      //             <Spinner animation="border" role="status">
      //               <span className="visually-hidden">Loading...</span>
      //             </Spinner>
      //           ) : (
      //             "Login"
      //           )}
      //         </button>
      //       </form>
      //       <h6 className="text-center text-white">
      //         New user? <Link to="/register"> Please register!</Link>
      //       </h6>
      //     </div>
      //   </div>
      // </div>
    );
  }
};

export default Login;
